import classNames from 'classnames';
import {
  disable as disableDarkMode, enable as enableDarkMode
} from 'darkreader';
import { LocalizedLink } from "gatsby-theme-i18n";
import * as React from "react";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import logo from '../../assets/icon/logo.png';
import { I } from "../../utils";
import { useViewport } from '../useViewport';
import * as styles from './index.module.less';

const list = [
  { name: 'en', text: 'lang.en' },
  { name: 'zh-cn', text: 'lang.zh' },
  { name: 'ja', text: 'lang.ja' },
  { name: 'ko', text: 'lang.ko' },
]
const Index = ({ data, style, navColor, ...props }) => {
  // window ? window.localStorage.getItem('darkMode') : 
  const [darkMode, setDarkMode] = React.useState('');
  React.useEffect(() => {
    const dark = window && window.localStorage.getItem('darkMode');
    if (parseInt(dark)) {
      enableDarkMode && enableDarkMode({
        brightness: 100,
        contrast: 90,
        sepia: 10,
      });
      if (dark !== darkMode) {
        setDarkMode(dark);
      }
    } else {
      disableDarkMode && disableDarkMode()
    }
  }, [darkMode, setDarkMode])
  const { size, scrollTop } = useViewport();
  const isM = size !== 'l';
  const [showMiniNav, setShowMiniNav] = React.useState(false);
  let arf = 0;
  // if (scrollTop > 300) {
  arf = scrollTop / 1200 + 0.3;
  arf = arf > 0.8 ? 0.8 : arf || 0.3;
  arf = isM ? 1 : arf;
  // }
  const onTiggle = () => {
    if (darkMode) window.localStorage.removeItem('darkMode');
    else window.localStorage.setItem('darkMode', 1);
    setDarkMode(darkMode ? 0 : 1);
  }

  return (
    <>
      <div className={styles.header}></div>
      <div className={styles.flex} style={{ ...style, color: navColor }} {...props}>
        <div className={classNames([styles.nav])}>
          <div className={styles.left}>
            {size !== 'l' && <i
              role="none"
              className="fa fa-navicon"
              style={{ fontSize: '28px', color: navColor }}
              onKeyDown={() => 0}
              onClick={() => setShowMiniNav(!showMiniNav)}
            ></i>}
            <a href="#home">
              <img className={styles.logo} src={logo} alt="" />
            </a>
          </div>
          <div className={styles.right}>
            {size === 'l' && <><LocalizedLink to="/#banner" style={{ color: navColor }}>{I('nav.home')}</LocalizedLink>
              <LocalizedLink to="/#about" style={{ color: navColor }}>{I('nav.aboutUs')}</LocalizedLink>
              <LocalizedLink to="/#partner" style={{ color: navColor }}>{I('nav.partner')}</LocalizedLink>
              <LocalizedLink to="/#project" style={{ color: navColor }}>{I('nav.project')}</LocalizedLink>
              <LocalizedLink to="/#contact" style={{ color: navColor }}>{I('nav.contact')}</LocalizedLink>
              <div className={styles.lang}>
                <a href="./#" style={{ color: navColor }}>{I('nav.lange')}<span className={styles.sjx}>▼</span></a>
                <ul>
                  <li className={styles.jiantou}></li>
                  <li style={{ paddingTop: '0px', borderTopLeftRadius: '4px', borderTopRightRadius: '4px' }}>
                    <LocalizedLink to="/" language="en" style={{ color: navColor }}>
                      <div className={styles.langLink}>
                        {I('nav.lange') === I('lang.en') ?
                          <div className={styles.jiantou2}></div> : <span style={{ width: '16px' }}></span>}
                        {I('lang.en')}
                      </div>
                    </LocalizedLink>
                  </li>
                  <li>
                    <LocalizedLink to="/" language="zh-cn" style={{ color: navColor }}>
                      <div className={styles.langLink}>
                        {I('nav.lange') === I('lang.zh') ?
                          <div className={styles.jiantou2}></div> : <span style={{ width: '16px' }}></span>}
                        {I('lang.zh')}
                      </div>
                    </LocalizedLink>
                  </li>
                  <li>
                    <LocalizedLink to="/" language="ja" style={{ color: navColor }}>
                      <div className={styles.langLink}>
                        {I('nav.lange') === I('lang.ja') ?
                          <div className={styles.jiantou2}></div> : <span style={{ width: '16px' }}></span>}
                        {I('lang.ja')}
                      </div>
                    </LocalizedLink>
                  </li>
                  <li style={{ paddingBottom: '30px', borderBottomLeftRadius: '4px', borderBottomRightRadius: '4px' }}>
                    <LocalizedLink to="/" language="ko" style={{ color: navColor }}>
                      <div className={styles.langLink}>
                        {I('nav.lange') === I('lang.ko') ?
                          <div className={styles.jiantou2}></div> : <span style={{ width: '16px' }}></span>}
                        {I('lang.ko')}
                      </div>
                    </LocalizedLink>
                  </li>
                </ul>
              </div>
            </>}
            {/* <a href="./#" style={{ cursor: 'pointer' }} onClick={onTiggle}>
              {darkMode ? <i className="fa fa-moon-o" /> : <i className="fa fa-sun-o" />}
            </a> */}
          </div>
        </div>
        {isM && <div
          className={classNames([styles.leftNav], { [styles.hover]: showMiniNav })}
          style={{ flexDirection: 'column' }}
        >
          <span className={styles.top}>
            <i
              className="fa fa-navicon"
              style={{ fontSize: '28px', color: '#fff' }}
              role="none"
              onClick={() => setShowMiniNav(!showMiniNav)}
            ></i>
          </span>
          <LocalizedLink to="/#banner" onClick={() => setShowMiniNav(false)} style={{ color: '#fff' }}>{I('nav.home')}</LocalizedLink>
          <LocalizedLink to="/#about" style={{ color: '#fff' }}>{I('nav.aboutUs')}</LocalizedLink>
          <LocalizedLink to="/#partner" style={{ color: '#fff' }}>{I('nav.partner')}</LocalizedLink>
          <LocalizedLink to="/#project" style={{ color: '#fff' }}>{I('nav.project')}</LocalizedLink>
          <LocalizedLink to="/#contact" style={{ color: '#fff' }}>{I('nav.contact')}</LocalizedLink>
          <div className={styles.lang}>
            <a href="javascript:void(0)">{I('nav.lange')}</a>
            <span className={styles.sjx}>▼</span>
            <ul>{list.map(p => <li key={p.text}>
              <LocalizedLink key={p.name} to="/" language={p.name} onClick={() => setShowMiniNav(!showMiniNav)} style={{ color: '#fff' }}>
                <div className={styles.langLink}>
                  {I('nav.lange') === I(p.text) ?
                    <div className={styles.jiantou2}></div> : <span style={{ width: '16px' }}></span>}
                  {I(p.text)}
                </div>
              </LocalizedLink>
            </li>)}
            </ul>
          </div>
        </div>}
        {isM && showMiniNav && <button
          className={styles.mask}
          onKeyDown={() => 0}
          onClick={() => setShowMiniNav(!showMiniNav)}>&nbsp;</button>}
      </div>
    </>
  )
}

export default Index
